const Info = (props) => <h1>hello {props.name}</h1>
const Wev = (props) => `whatever ${props.name}`

const Wrapped = (Comp) => (
  class Info extends Component {
    render() {
      return <Comp name="Just Me" />
    }
  }
)

const WrappedInfo = Wrapped(Info)
const WrappedWev = Wrapped(Wev)

